.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.topNav{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px dotted #000;
  .title{
    display: inline-block;
    margin:0 30px;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#tutorial-button{
  top: 108px;
  &:after,
  &:before { 
    content: " \2193";
    font-size: 20px;
  }
}

#top-button{
  bottom: 10px;
  &:after,
  &:before {
    content: " \2191";
    font-size: 20px;
  }
}

a{
  transition: 0.1s;
  position: fixed;
  left: 0;
  padding: 5px;
  background-color: #cecece;
  color: black;
  &:link,
  &:visited,
  &:hover,
  &:active { 
    text-decoration: none; 
    color: black;
  }
  &:hover{
    transform: translateX(3px);
  }
}

.operators-wrapper{
  width: 1024px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  .operators{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &.general{
      flex: 2 2;
    }
    .wrappable{
      height: 300px;
      display: flex;
      flex-direction: column;
      align-items: center;
      flex-wrap: wrap;
    }
  }
  .spec-operators{
    flex: 2 1;
  }
}

.right-panel{
  flex: 2 2;
}

button{
  transition: 0.1s;
  box-sizing: border-box;
  width: 110px;
  height: 30px;
  text-align: left;
  padding-left: 10px;
  border: none;
  margin: 0 2px 2px 0;
  font-size: 14px;
  &:hover{
    transform: translateX(3px);
  }
  &:disabled{
    transform: none;
  }
  &.operator-button{
    background-color: #98bf64;
  }
  &.often-operator-button{
    background-color: #b0fc38;
  }
  &.spec-operator-button{
    background-color: #3ded97;
  }
}

input[type="checkbox"]
{
  vertical-align:middle;
}

li{
  list-style: none;
}

.comma-delimited-list{
  width: 100%;
  height: 50px;
  border: 2px solid #90ee90;
  outline: none;
}

.add-list-to-query{
  margin-top: 5px;
  text-align: center;
  background: #90ee90;
}

#gsearch-wrapper{
  flex: 1;
  display: flex;
  flex-direction: column;
  border: 1px dotted #0096FF;
  padding:0 20px;
  align-items: flex-start;
  h3{
    color: #0096FF;
  }
  button{
    background-color: #0096FF;
    color: #fff;
    height: 36px;
    &:hover{
      transform: translateX(3px);
    }
  }
}

#gsearch{
  border: 2px solid #0096FF;
  width: 100%;
  display: block;
  height: 30px;
  font-size: 16px;
  outline: none;
}

#tutorial{
  text-align: left;
  width: 1024px;
  margin: 150px auto 0 auto;
}